'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import CardLogin_Login from '@/app/(auth)/register/components/cardLogin_Login';
import CardLogin_Register from '@/app/(auth)/register/components/cardLogin_Register';

export default function Register() {
    const [activeTab, setActiveTab] = useState<'login' | 'register'>('register');

    return (
        <div className="min-h-screen flex items-center justify-center bg-ccDarkGray">
            <div className="bg-ccGray p-8 rounded-xl shadow-md w-full max-w-lg border border-ccLightGray">

                <div className="relative flex items-center justify-between px-4 py-2 bg-ccLightGray rounded-xl text-white">

                    <button
                        onClick={() => setActiveTab('login')}
                        className={`w-1/2 text-xl py-2 px-4 z-10 transition-colors duration-300 ${
                            activeTab === 'login' ? 'text-black bg-white' : 'text-white'
                        }`}
                    >
                        Iniciar sesión
                    </button>

                    <button
                        onClick={() => setActiveTab('register')}
                        className={`w-1/2 text-xl py-2 px-4 z-10 transition-colors duration-300 ${
                            activeTab === 'register' ? 'text-black bg-white' : 'text-white'
                        }`}
                    >
                        Registrarse
                    </button>
                </div>

                <div className="mt-6 relative min-h-[200px]">
                    <AnimatePresence mode="wait">
                        {activeTab === 'login' ? (
                            
                                <CardLogin_Login onSubmit={function (email: string, password: string): void {
                                    throw new Error('Function not implemented.');
                                } } />
                           
                        ) : (
                            
                                <CardLogin_Register onSubmit={function (data: { name: string; email: string; password: string; }): void {
                                        throw new Error('Function not implemented.');
                                    } } />
                        
                        )}
                    </AnimatePresence>
                </div>
            </div>
        </div>
    );
}
